body {
  background: #eaeaea;
}
.swiper-container {
  width: 100%;
  height: 45.83vw;
  background-color: #1b358a;
}
.swiper-container .swiper-slide {
  position: relative;
}
.swiper-container .swiper-slide .text1 {
  position: absolute;
  top: 7.71vw;
  left: 14.38vw;
  height: 45px;
  font-family: SourceHanSansCN-Light;
  font-size: 2.5vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 45px;
  letter-spacing: 0vw;
  color: #ffffff;
}
.swiper-container .swiper-slide .text2 {
  position: absolute;
  top: 12.86vw;
  left: 14.38vw;
  width: 9.63vw;
  height: 0.31vw;
  background-color: #ffffff;
}
.swiper-container .swiper-slide .text3 {
  position: absolute;
  top: 17.71vw;
  left: 14.38vw;
  height: 3.06vw;
  font-family: SourceHanSansCN-Light;
  font-size: 1.25vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #ffffff;
}
.swiper-container .swiper-pagination {
  bottom: 3.32vw !important;
}
.swiper-container .swiper-pagination .swiper-pagination-bullet {
  background: #ffffff !important;
  opacity: 1;
}
.swiper-container .swiper-pagination .swiper-pagination-bullet-active {
  background: none !important;
  border: solid 0.09vw #ffffff;
}
.container-box {
  padding: 1.25vw 14.48vw 4.44vw;
}
.flex-v.left {
  padding: 3.02vw 3.33vw;
  color: #888888;
  height: 26.56vw;
  background-color: #ffffff;
  text-align: right;
  margin-bottom: 0;
}
.flex-v.right {
  padding: 3.02vw 3.33vw;
  color: #888888;
  height: 26.56vw;
  background-color: #f5f5f5;
  text-align: left;
}
.flex-v .text-1 {
  font-size: 1.56vw;
}
.flex-v .text-2 {
  font-size: 0.83vw;
}
.flex-v .text-3 {
  font-size: 0.83vw;
  line-height: 1.56vw;
}
.flex-v .text-4 {
  margin-top: 0;
  font-family: CourierNewPSMT;
  font-size: 0.73vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.56vw;
  letter-spacing: 0vw;
  color: #888888;
}
.space {
  background: #ffffff;
  padding: 1.18vw 2.91vw 10vw;
  margin-bottom: 4.32vw;
  margin-top: 1.25vw;
}
.space .inner {
  height: 0;
  width: 100%;
  padding-bottom: 45.12%;
  background-color: #626262;
  position: relative;
}
.space .inner > div {
  position: absolute;
  width: 100%;
  height: 100%;
}
.space .inner > div img {
  width: 100%;
  height: 100%;
}
@media (max-width: 768px) {
  .swiper-container {
    width: 100vw;
    height: 138.89vw;
    background-color: #1b358a;
  }
  .swiper-container .swiper-slide {
    position: relative;
  }
  .swiper-container .swiper-slide .text1 {
    position: absolute;
    top: 21.02vw;
    left: 5vw;
    height: 5.19vw;
    font-family: SourceHanSansCN-Light;
    font-size: 5.56vw;
    font-weight: normal;
    font-stretch: normal;
    line-height: 2.78vw;
    letter-spacing: 0vw;
    color: #ffffff;
  }
  .swiper-container .swiper-slide .text2 {
    position: absolute;
    top: 30.37vw;
    left: 5vw;
    width: 9.63vw;
    height: 0.56vw;
    background-color: #ffffff;
  }
  .swiper-container .swiper-slide .text3 {
    position: absolute;
    top: 40.37vw;
    left: 5vw;
    height: 3.06vw;
    font-family: SourceHanSansCN-Light;
    font-size: 2.78vw;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0vw;
    color: #ffffff;
  }
  .swiper-container .swiper-pagination {
    bottom: 15.93vw !important;
  }
  .swiper-container .swiper-pagination .swiper-pagination-bullet {
    background: #ffffff !important;
    opacity: 1;
  }
  .swiper-container .swiper-pagination .swiper-pagination-bullet-active {
    background: none !important;
    border: solid 0.09vw #ffffff;
  }
  .container-box {
    padding: 2.22vw 0vw;
  }
  .flex-v.left {
    padding: 5.19vw 4.54vw 3.89vw;
    color: #888888;
    height: 30.65vw;
    background-color: #ffffff;
    text-align: left;
    margin-bottom: 2.22vw;
  }
  .flex-v.right {
    padding: 6.3vw 5.74vw 4.63vw 5.93vw;
    color: #888888;
    height: auto;
    background-color: #f5f5f5;
    text-align: left;
  }
  .flex-v .text-1 {
    height: 2.69vw;
    font-size: 2.78vw;
    line-height: 2.78vw;
    color: #888888;
  }
  .flex-v .text-2 {
    font-size: 1.48vw;
    line-height: 2.78vw;
  }
  .flex-v .text-3 {
    font-size: 2.78vw;
    line-height: 5.56vw;
    color: #888888;
  }
  .flex-v .text-4 {
    height: auto;
    margin-top: 9.7vw;
    font-family: CourierNewPSMT;
    font-size: 2.22vw;
    line-height: 4.44vw;
    color: #888888;
  }
  .space {
    background: #ffffff;
    padding: 3.33vw 3.61vw;
  }
  .space .inner {
    height: 0;
    width: 100%;
    padding-bottom: 45.12%;
    background-color: #626262;
    position: relative;
  }
  .space .inner > div {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .space .inner > div img {
    width: 100%;
    height: 100%;
  }
}
